import { MSG_FETCH_CSRF_HEADERS_FAILED } from "../../utils/toastMessages";
import useToast from "../useToast";
import { fetchCsrfToken } from "../../services/api/PlantAssignment/fetchCsrfService";
import { callEnoviaWebService, fetchData } from "../../utils/helpers";
import { useEffect, useState } from "react";

// Add this configuration object at the top of your file (outside the component)
const OPERATION_CONFIG = {
  "1": { // Physical Product/Raw Material
    endpoint: "/resources/v1/modeler/dseng/dseng:EngItem/search",
    systemAttributes: [
      {
        name: "description",
        nls: "Description", 
        groupNLS: "System Attributes",
      },
      {
        name: "Part Number",
        nls: "EIN Number",
        groupNLS: "System Attributes",
      },
      {
        name: "type",
        nls: "Type",
        groupNLS: "System Attributes", 
      },
      {
        name: "title",
        nls: "Title",
        groupNLS: "System Attributes",
      },
      {
        name: "collabspaceTitle",
        nls: "Collaborative Space",
        groupNLS: "System Attributes",
      },
    ]
  },
  "2": { // Product Structure
    endpoint: "/resources/v1/modeler/dseng/dseng:EnterpriseReference/search",
    systemAttributes: [
      {
        name: "parentId", 
        nls: "Parent ID",
        groupNLS: "System Attributes",
      },
      {
        name: "childId",
        nls: "Child ID",
        groupNLS: "System Attributes", 
      },
      {
        name: "relationshipType",
        nls: "Relationship Type",
        groupNLS: "System Attributes",
      }
    ]
  },
  "3": { // Document
    endpoint: "/resources/v1/modeler/dscfg/dscfg:Document/search",
    systemAttributes: [
      {
        name: "type",
        nls: "Type",
        groupNLS: "System Attributes",
      },
      {
        name: "title",
        nls: "Title",
        groupNLS: "System Attributes",
      },
      {
        name: "description",
        nls: "Description",
        groupNLS: "System Attributes",
      },
      {
        name: "collabspaceTitle",
        nls: "Collaborative Space",
        groupNLS: "System Attributes",
      }
    ]
  },
  "4": { // Product-Document
    endpoint: "/resources/v1/modeler/dseng/dseng:EnterpriseReference/search",
    systemAttributes: [
      {
        name: "productId",
        nls: "Product ID",
        groupNLS: "System Attributes", 
      },
      {
        name: "documentId",
        nls: "Document ID",
        groupNLS: "System Attributes",
      },
      {
        name: "relationshipType", 
        nls: "Relationship Type",
        groupNLS: "System Attributes",
      }
    ]
  }
};

const useMassUpload = () => {
  const { showErrorToast } = useToast();
  const [mappedAttributes, setMappedAttributes] = useState([]);
  const ENOVIA_BASE_URL = process.env.REACT_APP_ENOVIA_BASE_URL;

  // Update fetchColumnMapping to accept operationType
  const fetchColumnMapping = async (operationType = "1") => {
    try {
      console.log(`Fetching column mapping for operation type: ${operationType}`);
      
      // Get operation-specific config
      const config = OPERATION_CONFIG[operationType] || OPERATION_CONFIG["1"];
      
      // Get CSRF Headers
      const headers = await fetchCsrfToken();
      if (!headers) {
        showErrorToast(MSG_FETCH_CSRF_HEADERS_FAILED);
        return;
      }

      // Use operation-specific endpoint
      const metadataResponse = await callEnoviaWebService(
        "GET",
        `${ENOVIA_BASE_URL}${config.endpoint}?%24top=1`,
        "",
        headers
      );

      if (!metadataResponse || !metadataResponse.output) {
        throw new Error(`Failed to fetch metadata for operation ${operationType}.`);
      }
      console.log(`Metadata Member for operation ${operationType}:`, metadataResponse.output.member);

      const objectId = metadataResponse.output?.member?.[0]?.id;
      if (!objectId) {
        throw new Error("No object ID found in metadata.");
      }

      console.log("Extracted Object ID:", objectId);

      // Step 2: Call API 2 (POST) to Fetch Attributes - this stays the same
      const attributeResponse = await callEnoviaWebService(
        "POST",
        `${ENOVIA_BASE_URL}/resources/v1/collabServices/attributes/op/read?tenant=OI000186152&xrequestedwith=xmlhttprequest`,
        {
          busIDs: [objectId],
        },
        headers
      );

      if (!attributeResponse || !attributeResponse.output) {
        throw new Error("Failed to fetch attribute data.");
      }
      console.log(`Attribute Response for operation ${operationType}:`, attributeResponse.output);

      // Extract data
      const groupData = attributeResponse.output.results?.[0]?.groupData || [];

      // Use operation-specific system attributes
      const systemAttributes = config.systemAttributes;

      const attributeGroups = groupData.map((item) => item.groupNLS);
      console.log("Attribute Groups:", attributeGroups);

      // Get NLS values from API response
      const apiNlsValues = groupData.map((item) => item.nls);

      // Get NLS values from system attributes for this operation
      const systemNlsValues = systemAttributes.map((item) => item.nls);
      
      const allNLSValues = [...new Set([...apiNlsValues, ...systemNlsValues])];
      
      // Filter out system attributes from API response
      const filteredGroupData = groupData.filter(
        (attr) => attr.groupNLS !== "System Attributes"
      );

      // Merge filtered API attributes with operation-specific system attributes
      const mergeAttributes = [...filteredGroupData, ...systemAttributes];

      const relevantAttributes =
        mergeAttributes.filter(
          (attr) =>
            attr.deploymentExtension === true ||
            (attr.groupNLS && attr.groupNLS.trim() !== "")
        ) || [];

      const mappedData = relevantAttributes.map((attr) => ({
        uiLabel: attr.nls,
        backendName: attr.name || attr.backendName,
        group: attr.groupNLS || "General",
      }));

      // Create filtered dropdown options
      const dropdownOptions = mappedData.filter(
        (attr) => attr.group !== "System Attributes"
      );

      // Group by attribute groups
      const attributesByGroup = {};
      attributeGroups.forEach((group) => {
        attributesByGroup[group] = mappedData.filter(
          (attr) => attr.group === group
        );
      });

      // Set result with operation type included
      setMappedAttributes({
        allNLSValues,
        dropdownOptions,
        mappedData,
        attributesByGroup,
        groups: attributeGroups,
        operationType: operationType // Include operation type in result
      });
    } catch (error) {
      console.error(`Error fetching column mapping for operation ${operationType}:`, error);
      showErrorToast(error.message || `Error fetching column mapping for operation ${operationType}.`);
    }
  };

  useEffect(() => {
    fetchColumnMapping("1"); // Default to operation 1
  }, []);

  return { mappedAttributes, refreshMapping: fetchColumnMapping };
};

export default useMassUpload;
